import { getProfile, getArticles } from '@/api/profile'
import { mapState } from 'vuex'
export default {
  middleware: ['authenticated'],
  name: "UserProfile",
  data () {
    return {
      userProfile: '',
    }
  },
  async asyncData ({params, query}) {
    const limit = 5
    const page = Number.parseInt(query.page || 1)
    const username = params.username
    console.log(params.username)
    const tab = query.tab || 'my_article'
    console.log(tab)
    const loadArticles = tab === 'my_article'
    ? {
      author: username,
      limit,
      offset:(page - 1) * limit,
    }
    : {
      favorited: username,
      limit,
      offset: (page - 1) * limit,
    }
    const [ userInfo, article ] = await Promise.all([
      getProfile(username),
      getArticles(loadArticles)
    ])
    const userProfile = userInfo.data.profile
    const { articles, articlesCount } = article.data
    return {
      userProfile,
      limit, // 每页大小
      tab,
      page,
      articlesCount,
      articles
    }
  },
  computed: {
    totalPage () {
      return Math.ceil(this.articlesCount / this.limit)
    },
    ...mapState(['user'])
  },
  watchQuery: ['tab', 'page'],
};
